body,
#root {
  height: 100vh;
  box-sizing: border-box;
  margin: 0;
}

html {
  padding: 20px;
}

.personsContainer {
  display: flex;
  gap: 10px;
  align-items: center;
}

.deleteButton {
  height: 50%;
}

.error {
  background-color: lightgreen;
  width: max-content;
  padding: 5px;
  margin-bottom: 20px;
  outline: 1px solid green;
}
